

.source-image {
  width: full-width;
  position: absolute;
  top: 0;
  left: 0;
  color: white;

}

.spacer
{
    width: 100%;
    height: 7vw;
}

.centered {
  white-space: nowrap;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  color: white;
  font-size: 5vw;
  font-weight: bold;
  font-family: sans-serif;
  top: 50%;
  left: 50%;
  transform: translate(-500%, -50%);
}
.centered-two {
  white-space: nowrap;
  z-index: auto;
  position: absolute;
  color: white;
  font-size: 3vw;
  font-weight: bold;
  font-family: sans-serif;
  top: 60%;
  left: 50%;
  transform: translate(-50%, 0%);
}

.container {
  text-align: center;
  color: white;
  box-sizing: border-box
}
